/**
 * LastPage model events
 */

'use strict';

import {EventEmitter} from 'events';
import LastPage from './lastPage.model';
var LastPageEvents = new EventEmitter();

// Set max event listeners (0 == unlimited)
LastPageEvents.setMaxListeners(0);

// Model events
var events = {
  save: 'save',
  remove: 'remove'
};

// Register the event emitter to the model events
for(var e in events) {
  let event = events[e];
  LastPage.schema.post(e, emitEvent(event));
}

function emitEvent(event) {
  return function(doc) {
    LastPageEvents.emit(event + ':' + doc._id, doc);
    LastPageEvents.emit(event, doc);
  };
}

export default LastPageEvents;
